export default {
	props: {
		custom: {
		  type: Boolean,
		  default: false
		}, //自定义头部，否则沿用原生的头部样式
		style: {
			type: Object,
			default: function() {
				return{
					background: '#0071CF',
					fontColor: '#FFFFFF',
					iconColor: '#FFFFFF'
				}
			}
		}, //原生头部样式
		showBack: {
		  type: Boolean,
		  default: true
		}, //是否显示回退图标，默认显示
	},
	data() {
		return {
			lineHeight: '' //状态栏高度
		};
	},
	mounted() {
		const res = uni.getSystemInfoSync();
		this.lineHeight = res.statusBarHeight + 'px';
	},
	methods: {
		prevent() {}
	}
}
